<template>
  <form class="my-5">
    <h3 class="text-muted my-5">添加新的pizza:</h3>
    <div class="form-group row my-1">
      <label class="col-sm-1">品种</label>
      <div class="col-sm-11">
        <input ref = 'input' type="text" class="form-control" v-model="newPizza.name" />
      </div>
    </div>
    <div class="form-group row my-3">
      <label class="col-sm-1">描述</label>
      <div class="col-sm-11">
        <textarea ref = 'input' rows="4" class="form-control" v-model="newPizza.description"></textarea>
      </div>
    </div>
    <p>
      <strong>选项1</strong>
    </p>
    <div class="form-group row my-3">
      <label class="col-sm-1">尺寸</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.size1" />
      </div>
    </div>
    <div class="form-group row my-3">
      <label class="col-sm-1">价格</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.price1" />
      </div>
    </div>

    <p>
      <strong>选项2</strong>
    </p>
    <div class="form-group row my-3">
      <label class="col-sm-1">尺寸</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.size2" />
      </div>
    </div>
    <div class="form-group row my-3">
      <label class="col-sm-1">价格</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.price2" />
      </div>
    </div>
    <div class="form-group row my-3">
      <button @click="addMenu" type="button" class="btn btn-success btn-block">添加</button>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      newPizza: {}
    };
  },
  methods: {
    addMenu() {
      let data = {
        name: this.newPizza.name,
        description: this.newPizza.description,
        options: [
          {
            size: this.newPizza.size1,
            price: this.newPizza.price1
          },
          {
            size: this.newPizza.size2,
            price: this.newPizza.price2
          }
        ]
      };
      // 将新添加的数据push到menu中
      // this.$axios.post('menu.json',data)
      //   .then(res => { this.$store.commit('pushToMenuItems',data) })
      // 这里用fetch请求数据
      fetch("https://my-app-8f064.firebaseio.com/menu.json", {
        method: "POST",
        headers: {
          "Content-type": "application/json"
        },
        body: JSON.stringify(data)
      })
        .then(res => res.json())
        // 将新添加的数据push到menu中
        .then(() => { this.$store.commit('pushToMenuItems',data) })
        .catch(err => console.log(err));
        this.newPizza = {};
   }
  }
};
</script>

