<template>
  <div class="row">
    <div class="col-sm-12 col-md-8">
      <!-- pizza-component -->
      <new-pizza />
    </div>
    <div class="col-sm-12 col-md-4 my-5">
      <h3 class ="text-muted">Menu:</h3>
      <table class="table my-5">
        <thead class="thead">
          <tr>
            <th>品种</th>
            <th>删除</th>
          </tr>
        </thead>
        <tbody class="tbody" v-for = '(item,index) in getMenuData' :key = "index">
          <tr>
            <td>{{item.name}}</td>
            <button  @click = 'deteleData(item)' class="btn btn-sm btn-outline-danger btn-sm mt-2 ml-3">&times;</button>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import newPizza from "./NewPizza";
export default {
  components: {
    newPizza
  },
  data(){
    return{
      // getMenuData: {},
    }
  },
  computed: {
    getMenuData:{
      get(){
       return this.$store.state.menuItems;
      },
      set(){

      }
    }
  },
  created() {
     fetch("https://my-app-8f064.firebaseio.com/menu.json")
      .then(res => {
        return res.json()
      })
      .then(data => {
        let menuData = [];
        for( let key in data){
          data[key].id = key;
          menuData.push(data[key]);
        }
        // this.getMenuData = menuData;
        this.$store.commit('setMenuItem',menuData)
      });

  },
  methods: {
    deteleData(item){
      fetch("https://my-app-8f064.firebaseio.com/menu/" + item.id +".json",{
        method: 'DELETE',
         headers: {
          "Content-type": "application/json"
        }
      }).then(res => res.json)
        // .then(()=>{ this.$router.push({name: 'Menu'}) })
        .then( () =>{
          this.$store.commit('removeMenuItems',item);
        })
        .catch(err => console.log(err))
    }
  },  
